nse: none (public domain)\n*/\n\n@import url('https://fonts.googleapis.com/css2?family=Noto+Serif:ital,wght@0,400;0,700;1,400;1,700&family=Red+Hat+Mono:ital,wght@0,300;0,400;0,500;1,300;1,400;1,500&family=Roboto:wght@400;500;700&display=swap');\n\nhtml, body, div, span, applet, object, iframe,\nh1, h2, h3, h4, h5, h6, p, blockquote, pre,\na, abbr, acronym, address, big, cite, code,\ndel, dfn, em, img, ins, kbd, q, s, samp,\nsmall, strike, strong, sub, sup, tt, var,\nb, u, i, center,\ndl, dt, dd, ol, ul, li,\nfieldset, form, label, legend,\ntable, caption, tbody, tfoot, thead, tr, th, td,\narticle, aside, canvas, details, embed, \nfigure, figcaption, footer, header, hgroup, \nmenu, nav, output, ruby, section, summary,\ntime, mark, audio, video {\n\tmargin: 0;\n\tpadding: 0;\n\tborder: 0;\n\tfont-size: 100%;\n\tfont: inherit;\n\tvertical-align: baseline;\n}\n/* HTML5 display-role reset for older browsers */\narticle, aside, details, figcaption, figure, \nfooter, header, hgroup, menu, nav, section {\n\tdisplay: block;\n}\nbody {\n\tline-height: 1;\n  overflow-x: hidden;\n}\nol, ul {\n\tlist-style: none;\n}\nblockquote, q {\n\tquotes: none;\n}\nblockquote:before, blockquote:after,\nq:before, q:after {\n\tcontent: '';\n\tcontent: none;\n}\ntable {\n\tborder-collapse: collapse;\n\tborder-spacing: 0;\n}\n\n/* ROOT */\n\nhtml {\n  font-size: 16px;\n  overflow-y: scroll;\n}\n\ni {\n  font-style: italic;\n}\n\na:hover, .navlink:hover {\n  cursor: pointer;\n}\n\n/* NAV */\n\nbody {\n  background-color: black ;\n  overflow-y: overlay;\n}\n\n\nnav {\n  width: 100%;\n  margin-bottom: 2rem;\n  z-index: 9999;\n}\n.navbar {\n  background: black;\n  display: flex;\n  justify-content: space-between;\n  align-items: baseline;\n  margin-top: 20px;\n  \n \n  \n}\n\n.navlinks {\n  margin-left: 3rem;\n  width: 33%;\n  order: 0;\n}\n\n.navlinks li {\n  padding: 0 1.5rem;\n  display: inline-block;\n  \n}\n\n.navlinks li a {\n  color: #fff6ce;\n  font-family: Roboto;\n  text-decoration: none;\n\n  font-size: 1.25rem;\n}\n\n.navlinks li :not(.selected):after {\n  margin-top: 10px;\n  content: \"\";\n  display: block;\n  height: 4px;\n  width: 0px;\n  transition: 0.3s;\n}\n\n.navlinks li :not(.selected):hover:after, .selected:after {\n  position: relative;\n  display: block;\n  margin-top: 10px;\n  width: 100%;\n  height: 4px;\n  content: \"\";\n  background-color:#fff6ce; ;\n  cursor: pointer;\n}\n\n\n\n.hamburger {\n  display: none;\n  padding: 3rem;\n  z-index: 9999;\n}\n\n.bar {\n  display: block;\n  width: 2rem;\n  height: 0.25rem;\n  margin: 0.5rem auto;\n  transition: all 0.3s ease-in-out;\n  background-color: #fff6ce;\n}\n\n.logo {\n  justify-self: center;\n  order: 1;\n}\n\n.mobile-logo {\n  display: none;\n}\n\n.logo img {\n  max-height: 6rem;\n  width: auto;\n  margin-bottom: -0.25rem;\n}\n\n.social-buttons {\n  margin-right: 3rem;\n  display: flex;\n  order: 2;\n  width: 33%;\n  justify-content: right;\n\n}\n\n.social-buttons img{\n  padding: 0 2rem;\n  display: inline-block;\n  width: 1.5rem;\n  height: auto;\n}\n.social-buttons img:after {\n  margin-top: 10px;\n  content: \"\";\n  display: block;\n  height: 4px;\n  width: 0px;\n  transition: 0.3s;\n}\n\n.social-buttons img:hover:after{\n  position: relative;\n  display: block;\n  margin-top: 10px;\n  width: 100%;\n  height: 4px;\n  content: \"\";\n  background-color:#fff6ce; \n}\n\n\n\n@media only screen and (max-width: 1500px) {\n  nav {\n    position: fixed;\n    background-color: black;\n    top: 0;\n    overflow-x: hidden;\n    \n  }\n\n  .navbar {\n    position: fixed;\n    left: -100%;\n    flex-direction: column;\n    justify-content: space-between;\n    margin-top: 7rem;\n    padding-top: 6em;\n    width: 100%;\n    text-align: left;\n    transition: 0.3s;\n    align-items: start;\n    height: 60vh;\n    overflow-y: scroll;\n    overflow-x: hidden;\n    \n  }\n\n  .navlinks {\n    display: flex;\n    flex-direction: column;\n    justify-content: space-between;\n    height: 60%;\n    